import _ from 'lodash';
import { shuffleArray} from './constantsPhatAm';


function getChoices(a, b){
  let choices = [];
  
  choices.push(a+b);

  for(var i=a+(a>2?1:3);i > 0 &&  choices.length < 4;i-- ){
    for(var j=b+1;j  > 0 &&  choices.length < 4;j-- ){
      if (!choices.find((e) => e === i+j)){
        choices.push(i+j);
      }
    }
  }

  choices = shuffleArray( choices);
  
  return choices;
}

export function getExercisesCongPv10 () {
  var exercises = [];
  for(var i=0;i<10;i++){
    for(var j=10-i;j>0;j--){
      exercises.push({ id: `${i}_${j}`, 
        question: `${i} + ${j} = ?`, 
        choices: getChoices(i,j), 
        correctAnswer: i + j });
      
    }
  }
}

export function getExercisesCongSoNhoHon10 () {
  var exercises = [];
  for(var i=0;i<10;i++){
    for(var j=0;j<10;j++){
      exercises.push({ id: `${i}_${j}`, 
        question: `${i} + ${j} = ?`, 
        choices: getChoices(i,j), 
        correctAnswer: i + j });
      
    }
  }

  console.log('getExercisesCongSoNhoHon10',exercises);

  return shuffleArray(exercises);
};
